footer{
	position:fixed;
	width:100%;
	height:1rem;
	left:0;
	bottom:0;
	background:#fff;
	display:flex !important;
    z-index:100;
    border-top:1px solid #e4e4e4;
    align-items: center;
	div{
		flex:1;
		height:0.9rem;
		a{
			display: block;
			width:100%;
			height:100%;
			display:flex;
			flex-direction:column;
			box-sizing:border-box;
			padding:0.06rem 0;
			text-align:center;
			font-family:"微软雅黑";
			color:#545454;
			position: relative;
			i{
			width:100%;
			height:0.45rem;
			line-height:0.45rem;
			font-size:0.42rem !important;
			font-weight: 600;
			}
			span{
				width:100%;
				height:0.35rem;
				line-height:0.35rem;
				font-size:0.24rem;
				font-weight: bold;
			}
			.hint{
				position: absolute;
				width: 0.15rem;
				height: 0.15rem;
				border-radius: 50%;
				right: 0.32rem;
			}
		}
		.nav-active,.router-link-exact-active{
			color:#ff5a5f;
		    // border-color:yellowgreen;
		}

	}
}